/* eslint-disable react-hooks/exhaustive-deps */
import React, { Fragment, useEffect, useState, useContext } from 'react'
import { normalFormat } from '../../dateParser';
import { useAsyncReference, useScroll } from '../../hooks';
import { EReviewOrderBy, reviewPath } from "../../callers/reviewCaller";
import ReviewList from './ReviewList';
import ReviewOrderBySelect from './ReviewOrderBySelect';
import { IMovie } from '../../../common/movie';
import { ECallType, postCaller } from '../../callers/caller';

interface IMovieReviewFeed{
    movie: IMovie;
}

export const MovieReviewFeed: React.FC<IMovieReviewFeed> = ({movie}: IMovieReviewFeed) => {
    const [reviews, setReviews] = useAsyncReference([]);
    const [orderBy, setOrderBy] = useState<EReviewOrderBy>(undefined);
    const [reviewHeader, setReviewHeader] = useState<string>("");
    const reviewPerPage = 10;
    const [reviewCaller, reviewSignal] = postCaller();
    const [addScroll, removeScroll] = useScroll(0.9, getMoreReviews);

    useEffect(()=>{
        return ()=> {
            removeScroll();
            reviewSignal.abort();
        }
    }, []);

    useEffect(()=>{
        if(new Date().getTime() >= new Date(movie.releaseDate).getTime()){
            if(orderBy === EReviewOrderBy.popIndex){
                if(movie.id > 0) getReviews();
            } else {
                setOrderBy(EReviewOrderBy.popIndex);
            }          
        } else {
            setReviewHeader(`Reviews will be available on ${normalFormat(movie.releaseDate.toString())}`);
        } 
        return ()=> {
            removeScroll();
        }
    }, [movie.id, movie.releaseDate]);

    useEffect(()=>{
        if(movie.id > 0 && orderBy != null){
            getReviews();
        }
        return ()=> {
            removeScroll();
        }
    }, [orderBy]);

    async function getReviews(){
        const requestedReviews = await reviewCaller({ movieId: movie.id, limit: reviewPerPage, offset: 0, orderby: orderBy}, reviewPath.selectReviewsByMovie);
        if(requestedReviews){
            setReviews(requestedReviews);
            setReviewHeader("Reviews");
            if(requestedReviews.length === reviewPerPage) addScroll();
        } else {
            setReviewHeader("Server errror: could not download reviewdata.");
        }
    }

    async function getMoreReviews(){
        removeScroll();
        const requestedReviews = await reviewCaller({ movieId: movie.id, limit: reviewPerPage, offset: reviews.current.length, orderby: orderBy}, reviewPath.selectReviewsByMovie);
        if(requestedReviews){
            setReviews([...reviews.current, ...requestedReviews]);
            if(requestedReviews.length === reviewPerPage) addScroll();
        } else {
            setReviewHeader("Server errror: could not download reviewdata.");
        }
    }

    async function deleteReview(reviewId: number){
        if(await reviewCaller({ id: reviewId }, reviewPath.deleteReview, ECallType.ISOK)){
            setReviews(reviews.current.filter(r=> r.id !== reviewId));
        }
    }

    return (
        <div>
            <h2 className="center-text">{reviewHeader}</h2>
            {new Date().getTime() >= new Date(movie.releaseDate).getTime() ? 
            (<Fragment>
                <ReviewOrderBySelect defaultValue={orderBy} onUpdate={(newValue: EReviewOrderBy)=> { setOrderBy(newValue); }} />
                <ReviewList onDelete={deleteReview} reviews={reviews.current} />
            </Fragment>): 
            (<Fragment></Fragment>)}    
        </div>
    )
}
export default MovieReviewFeed;